
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapState,mapGetters,mapMutations } from 'vuex';
export default {
name: 'app',
components: {
Tab,
TabItem,
XHeader,
Actionsheet
},
created:function(){
var loginStatus=window.localStorage.getItem('loginStatus');

if(loginStatus){
this.mutationIsLogined();
this.mutationLonginname(window.localStorage.getItem('loginname'));
this.mutationAccesstoken(window.localStorage.getItem('accesstoken'))
}
},
methods:{
...mapMutations(['mutationIsLogined','mutationLonginname','mutationAccesstoken']),
routeto(index){

this.$router.push('/topiclist/'+this.topics[index].name);
},

ShowMoreMenus(){
this.isShowMoreMenus = true;
},
handleClickMoreMenu(menukey,menuitem){
this.$router.push('/'+menukey);
}
},
computed:{
...mapGetters(['moreMenus']),
...mapState(['isShowMoreMenus','isLogined','unLoginedMoreMenus','loginedMoreMenus','topics']),
isShowMoreMenus:{ //getset

get:function(){return this.$store.state.isShowMoreMenus},

set:function(nval){
this.$store.commit('mutationIsShowMoreMenus',nval)
}
},

},
data:function(){
return{

}
}
}



