import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
      isShowMoreMenus:false,//actionsheet
      isLogined:false,//
      loginname:'',//
      accesstoken:'',//token
      unLoginedMoreMenus:{   //
        login:''
      },
      loginedMoreMenus:{ //
        center:'',
        msg:'',
        logout:''
      },
      topics:[   //tab
        {name:'all',desc:''},
        {name:'good',desc:''},
        {name:'share',desc:''},
        {name:'ask',desc:''},
        {name:'job',desc:''},
        {name:'test',desc:''}
      ]
    },

    getters:{
        moreMenus(state){
            // storestate
            if(state.isLogined){
                return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
          }
    },
    mutations:{
        // statemutation
        mutationIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
        },
        // 
        mutationIsLogined:function(state){
          state.isLogined=true;
        },
        // 
        mutationLonginname:function(state,payload){
          state.loginname=payload;
        },
        // accesstoken
        mutationAccesstoken:function(state,payload){
          state.accesstoken=payload;
        },
        // 
        mutationLogout:function(state){
          state.isLogined=false;
          state.loginname='';
          state.accesstoken='';
        }
    },
    actions:{}
})
export default store;


